<template lang="html">

  <div class="tabNav" >
    <ul class="nav nav-tabs">
      <li v-for="tab in tabs" :key="tab" class="nav-item">
        <div class="navbar-brand">


        <a class="tab_link" :class="{ active: tab === selected}" @click="setTab(tab)">
          {{ tab }}
        </a>
        </div>
      </li>
    </ul>

    <slot ></slot>

  </div>

</template>

<script>
export default {
  props: {
    tabs: {
      type: Array,
      required: true,
    },
    selected:{
      type: String,
      required: true,
    },
  },
  methods: {
    setTab(tab){
      this.$emit('selected',tab);
    }
  },

}
</script>

<style lang="css" scoped>

.tabNav{
  padding-top: 15px;
}
.tab_link{
  border-color: #000410;
  border: 10px;
  text-align: center;
  padding: 5px 15px;
  background-color: #003070;
  color: #ffffff;
  text-decoration: none;
  border-style: solid;
  border-color: black;
  border-width: medium;

}
.tab_link:hover{
  background-color: #000410;
}
.tab_link:visited {
  background-color: #000033;
}
</style>
